import uCharts from '@/components/u-charts/u-charts.js';
import  { isJSON } from '@/common/checker.js';
var _self;
var canvaColumn=null;
  
export default {
	data() {
		return {
			cWidth:'',
			cHeight:'',
			pixelRatio:1,
			textarea:''
		}
	},
	onLoad() {
		_self = this;











		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(500);
		this.getServerData();
	},
	methods: {
		getServerData(){
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				data:{
				},
				success: function(res) {
					console.log(res.data.data)
					let Column={categories:[],series:[]};
					//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
					// Column.categories=res.data.data.LineA.categories;
					// Column.series=res.data.data.LineA.series;
					Column.categories=['现有库存','上期结存','本期入库','本期出库','本期在途','可派货量']
					Column.series= [{data: [15.4500,20.2480,45.6790,37.6790,43.5679,34.4324],name: "库存"}]
					_self.textarea = JSON.stringify(res.data.data.LineA);
					_self.showColumn("canvasColumn",Column);
				},
				fail: () => {
					_self.tips="网络错误，小程序端请检查合法域名";
				},
			});
		},
		showColumn(canvasId,chartData){
			canvaColumn=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'column',
				fontSize:11,
				padding:[5,15,15,15],
				legend:{
					show:true,
					position:'top',
					float:'center',
					itemGap:30,
					padding:5,
					margin:5,
					//backgroundColor:'rgba(41,198,90,0.2)',
					//borderColor :'rgba(41,198,90,0.5)',
					borderWidth :1
				},
				dataLabel:true,
				dataPointShape:true,
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				enableScroll: true,
				xAxis: {
					disableGrid:false,
					type:'grid',
					gridType:'dash',
					itemCount:4,
					scrollShow:true,
					scrollAlign:'left',
				},
				yAxis: {
					//disabled:true
					// gridType:'dash',
					// splitNumber:4,
					// min:10,
					// max:180,
					format:(val)=>{return val.toFixed(0)+'吨'}//如不写此方法，Y轴刻度默认保留两位小数
				},
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				extra: {
					column: {
						type:'group',
						width: _self.cWidth*_self.pixelRatio*0.45/chartData.categories.length
					}
				},
			});
			
		},
		touchColumn(e){
			canvaColumn.scrollStart(e);
		},
		moveColumn(e) {
			canvaColumn.scroll(e);
		},
		touchEndColumn(e) {
			canvaColumn.scrollEnd(e);
			canvaColumn.touchLegend(e, {
				animation:true,
			});
			canvaColumn.showToolTip(e, {
				format: function (item, category) {
					return category + ' ' + item.name + ':' + item.data 
				}
			});
		},
		changeData(){
			if(isJSON(_self.textarea)){
				let newdata=JSON.parse(_self.textarea);
				canvaColumn.updateData({
					series: newdata.series,
					categories: newdata.categories,
					scrollPosition:'right',
					animation:false
				});
			}else{
				uni.showToast({
					title:'数据格式错误',
					image:'../../../static/images/alert-warning.png'
				})
			}
		}
	}
}
